<template lang="html">
  <div class="iwt-image-wrapper">
    <img class="iwt-image" :src="img_path" alt="">
    <p class="iwt-title" :style="cssProps">{{title}}</p>
  </div>
</template>

<script>
export default {
  name: 'stImageAndTitle',
  props: {
    img_path: String,
    title: String,
    title_align: {
      default: "right",
      type: String
    },
    title_bg: {
      default: "white",
      type: String
    }
  },
  computed: {
    cssProps() {
      let d = {};
      if (this.title_align=='left') {
        d['left'] = '0';
      }
      else {
        d['right'] = '0';
      }
      d['background-color'] = this.title_bg;
      return d;
    }
  }
}
</script>

<style lang="css">

.iwt-title {
  position: absolute;
  bottom: 0;
  /*right: 0;
  background-color: white;*/
  border: 1px solid;
  margin: 0;
  padding: 0.25em 0.5em;
  border-radius: inherit;
  box-sizing: border-box;
}
.iwt-image-wrapper {
  position: relative;
  border-radius: inherit;
}
.iwt-image {
  width: 100%;
  border-radius: inherit;
  display: block;
}
</style>
