<template lang="html">
  <nav class="header-nav">
    <a href="/"><stLogo/></a>
    <stSearchBar placeholder="Поиск по сайту" :maximize="true"/>
    <ul class="nav-menu">
      <li><a href="/catalog">Практики</a></li>
      <li><a @click="isSignInOpened=true" href="#">Войти</a></li>
      <li><a @click="isSignUpOpened=true" href="#">Регистрация</a></li>
    </ul>
  </nav>
  <stModalSignIn v-if="isSignInOpened" @close="isSignInOpened=false"/>
  <stModalSignUp v-if="isSignUpOpened" @close="isSignUpOpened=false"/>
</template>

<script>
import stLogo from '@/components/common/logo.vue'
import stSearchBar from '@/components/common/search.vue'
import stModalSignIn from '@/components/common/blocks/sign_in.vue'
import stModalSignUp from '@/components/common/blocks/sign_up.vue'

export default {
  name: 'stHeaderNav',
  components: {
    stLogo,
    stSearchBar,
    stModalSignIn,
    stModalSignUp
  },
  data() {
    return {
      isSignInOpened: false,
      isSignUpOpened: false
    }
  },
  methods: {
    openModalSignIn() {
      this.isSignInOpened=true;
    },
    openModalSignUp() {
      this.isSignUpOpened=true;
    }
  }
}
</script>

