  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0.5em 5em;
  background-color: white;
  position: sticky;
  z-index: 10;
  top: 0;
  left: 0;
}
.nav-menu {
  list-style-type: none;
  overflow: hidden;
  margin: 0;
  padding: 0;
}
